import DeleteOutline from "@mui/icons-material/DeleteOutline";
import Edit from "@mui/icons-material/EditOutlined";
import { ListItemButton, ListItemText } from "@mui/material";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
import { useState } from "react";
import { useSearchParams } from "react-router-dom";
import { useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveTable from "../../../../components/ResponsiveTable";
import Text from "../../../../components/Text";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";
import useGetRowNumber from "../../../../utils/useGetRowNumber";
import useNavigateTask from "../../../../utils/useNavigateTask";


const DataTable = ({
    loading,
    data,
    meta,
    infiniteScrollProps,
    onClickEdit,
    onClickDelete,
}) => {
    const { inject } = useAuth();

    const [searchParams, setSearchParams] = useSearchParams();

    const [sort, setSort] = useState(
        searchParams.has('sort') ? searchParams.get('sort') : '-createdAt'
    );

    useAfterFirstRender(() => {
        setSearchParams({
            ...Object.fromEntries(searchParams),
            sort
        })
    }, [sort]);

    const getRowNumber = useGetRowNumber();

    const navigateTask = useNavigateTask();
    const taskDetails = useTask('People.details');


    return (
        <>
            <ResponsiveTable
                data={data}
                loading={loading}
                infiniteScrollProps={infiniteScrollProps}
                definitions={sortBy([
                    {
                        title: '#',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 100
                        },
                        renderCell: (row, index) => getRowNumber(index),
                        priority: 100
                    }, {
                        field: 'code',
                        title: 'کد',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 100
                        },
                        priority: 200
                    }, {
                        field: 'fullName',
                        title: 'نام و نام خانوادگی',
                        rowCellProps: {
                            width: 200
                        },
                        priority: 300
                    },
                    ...flatten((inject('Customers', 'CustomersList.definitions') || []))
                ], o => o.priority)}
                renderSheetTitle={row => <Text>{row.fullName}</Text>}
                renderListItemChildren={(row) => (
                    <ListItemButton onClick={() => navigateTask(taskDetails(row))}>
                        <ListItemText primary={row.fullName} />
                    </ListItemButton>
                )}

                tableRowProps={row => ({
                    hover: true,
                    striped: true,
                    onClick: () => navigateTask(taskDetails(row))
                })}

                paginationProps={{
                    count: meta.total,
                    rowsPerPage: searchParams.get('perPage') || 25,
                    page: searchParams.get('page') || 0,
                    onPageChange: (e, page) => setSearchParams({ ...Object.fromEntries([...searchParams]), page }),
                    onRowsPerPageChange: e => setSearchParams({ ...Object.fromEntries([...searchParams]), perPage: e.target.value })
                }}

                actions={[
                    {
                        title: 'ویرایش',
                        icon: <Edit />,
                        onClick: onClickEdit,
                        authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('person:edit')
                    },
                    {
                        title: 'حذف',
                        icon: <DeleteOutline />,
                        onClick: person => onClickDelete(person, false),
                        authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('person:delete')
                    }
                ]}
            />
        </>
    )
}

export default DataTable